const getToday = () => {
  const today = new Date()
  let dd = today.getDate()
  let mm = today.getMonth() + 1
  const yyyy = today.getFullYear()
  if (dd < 10) {
    dd = `0${dd}` 
  } else if (mm < 10) {
    (
      mm = `0${mm}`
    )
  }  
  return `${yyyy}/${mm}/${dd}`
}    

export const today = getToday()